<template>
  <v-navigation-drawer absolute permanent  right app class="float-left text-left">
    <v-treeview
      hoverable
      :items="items"
      activatable
      color="#e9e9e9"
      dense
      open-all  
    >
      <template v-slot:label="{ item }">
        <router-link :to="item.link" :class="`text-style ${item.type != 'device' ? 'font-weight-bold' : '' } ${item.link == currentUrl ? 'active-item' : '' }`">{{ item.name }}</router-link>
      </template>
    </v-treeview>
  </v-navigation-drawer>
</template>

<script>
  export default {
    data: () => ({
      currentUrl: "",
    }),
    computed: {
      items () {
        return [
          { id: 1, name: 'All Networks', children: this.networksData, type:'rubix-master', link:"/rubix-driver/networks" },
        ]
      },
      networksData () {
        const { rdNetworks } = this.$store.state;
        return this.getTreeViewData(rdNetworks);
      },
    },
    watch: {
       '$route.path': {
        handler: function(path) {
          this.currentUrl = path;
        },
        deep: true,
        immediate: true
      },
   
    },
    mounted() {
    },
    methods: {
      getTreeViewData(networksData){
        if(!networksData || networksData && networksData.length == 0){
          return;
        }
        const result = networksData.map(nw=>{
          if(nw.devices && nw.devices.length > 0){
            const devices = [];
            nw.devices.forEach(de => {
              const device = { id: de.uuid, name: `Device: ${de.name}`, type: 'device', link: `/rubix-driver/devices/${de.uuid}/points` };
              devices.push(device);
            });
            return { id: nw.uuid, name: `Network: ${nw.name}`, children: devices, type: 'network', link: `/rubix-driver/networks/${nw.uuid}/devices` };
          }
          else{
            return { id: nw.uuid, name: `Network: ${nw.name}`, type: 'network', link: `/rubix-driver/networks/${nw.uuid}/devices` };
          }
        });
        return result;
      },
    
    },
  };
</script>
<style >
  .text-style{
    text-decoration: unset;
    color: white !important;
  }
  .active-item{
    /* background-color: #616161; */
  }
</style>


